* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: Arial, Helvetica, sans-serif;
  font-size: calc(10px + 2vmin);
  min-height: 100vh;
  width: 100%;
  z-index: -99;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: -webkit-gradient(linear, left bottom, right top, from(#b7dcf4), to(#fafcfc));
  background: linear-gradient(to top right, #b7dcf4, #fafcfc); }

.background-wrapper {
  width: 100%;
  max-height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  left: 0;
  z-index: -5; }
  .background-wrapper svg {
    width: 90%; }

footer {
  z-index: 10;
  width: 100%;
  text-align: center;
  position: relative;
  bottom: 0;
  left: 0; }
  footer a {
    text-decoration: underline;
    color: #2b50c5; }
  footer p {
    display: inline-block;
    padding: 10px 20px;
    background-color: #fbeaa5; }

.underline-link > a {
  text-decoration: none;
  position: relative; }
  .underline-link > a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -2px;
    left: 0;
    background-color: #2b50c5;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s; }
  .underline-link > a:focus:before, .underline-link > a:hover:before {
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1); }

.card-container {
  margin: 20px auto;
  z-index: -10;
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fafcfc;
  box-shadow: 10px 15px 20px #242424;
  border: 20px solid #f8c3b9;
  width: 90%; }
  @media screen and (min-width: 601px) {
    .card-container {
      width: 50%;
      min-width: 600px; } }
  .card-container .card {
    z-index: 50;
